.panelMantenimientos {

  width: 95%;
  height: 70%;
  margin: 0 auto;
}


.botonesMenu {
  align-items: center;
  width: 90%;
  margin-left: 5%;
  margin-right: 5%;
  align-content: center;
}

.filtromes {
  align-items: center;
  width: 90%;
  margin-left: 5%;
  margin-right: 5%;
  align-content: center;
}



.contenedor {
  display: flex;
  width: 100%;
  height: 200px;
}

.izquierda {
  width: 20%;
  align-items: center;
  text-align: center;
}

.derecha {
  width: 80%;
  background-color: #f8d7da;
  align-items: center;
  text-align: center;
}

.custom-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 1rem;
}

.form-item {
  display: flex;
  flex-direction: column;
}
